*{
    margin: 0;
    padding: 0px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.App{
    display: block;
    width: 100%;
    height: 90vh;

}

.leftcont,.rightcont{
    vertical-align: top;
    display: inline-block;
    width: 50%;
    height: 100%;
    padding:0px;
}

.text1,.text2{
    width: 90%;
    font-size: 20px;
    line-height: 1.3;
    font-family: Arial;
    color: #333333;
    height: 100%;
}
.text2{
    border: 1px #999999 solid;
    line-height: 1.5;
}

.box{
    padding-right: 5px;
}

